.navbar-profile-pic img {
 border-radius: 90px;
 height: 150px;
 width: 150px;
 display: flex;
 vertical-align: center;
 margin: 0 auto;
 margin-top: 20px;
}

.menu .nav-item{
    color: white;
    margin-top: 30px;
    margin-left: 25px;

}

.nav-link{
    margin-top: 20px;
    cursor: pointer;
} 

.nav-link svg{
    margin-right: 10px; 
}

.nav-link:hover{
    color: #f29f67;
}

.nav-link .active{
    color: #f29f67;
    padding-left: 10px;
    border-left: 5px solid #f29f67;
    text-decoration: none;
}