.project{
    margin: 40px 0px;
}

.project hr{
    color: #138781;
    width: 400px;
    margin: 10px auto;
}

.project h2{
    font-weight: bold;
    color: #138781;
}

#ads{
    margin: 30px 0px 30px 0;
}

#ads .card-notify-badge{
    position: absolute;
    margin-left: -10px;
    margin-top: -20px;
    background-color: #f29f67;
    text-align: center;
    border-radius: 30px;
    color: black;
    padding: 5px 10px;
    font-size: 14px;
}

.project #ads img{
    height:150px;
    width: 100%;
}

.card-detail-badge{
    background-color: #138781;
    border-radius: 30px;
    color: white;
    text-align: center;
    padding: 5px 10px;
}
.card-image-overly{
    font-size: 20px;
}
.card-image-overly span{
    display: inline-block;
    margin-left: 5px;
}

.ad-btn{
    display: block;
    text-align: center;
    background-color: #138781;
    border-radius: 80px;
    text-transform: uppercase;
    text-decoration: none;
    height: 40px;
    width: 150px;
    color: white;
    position: relative;
    margin: 20px auto ;
    line-height: 35px;
    overflow: hidden;
    font-size: 16px;
}

.project .card:hover{
    background-color: white;
    border-radius: 4px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    transition: all 0.5s ease;
}

.ad-btn:hover{
    background-color: white;
    color: #138781;
    border: 2px solid #138781;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    transition: all 0.5s ease;
}

/* Mobile View */

@media (max-width:600px){
    .project hr{
        color: #138781;
        width: 350px;
        margin: 10px auto;
    }
}