.educations{
    margin: 40px 0px;
    padding: 100px 0px;
    background-color: rgba(128, 128, 128, 0.101);
}

.educations hr{
    color: #138781;
    width: 400px;
    margin: 10px auto;
}

.educations h2{
    font-weight: bold;
    color: #138781;
}

.educations .vertical-timeline-element-title{
    color: #138781;
}

.educations .vertical-timeline-element-subtitle{
    color: #1e1e2c;
}

/* Mobile View */

@media (max-width:600px){
    .educations hr{
        color: #138781;
        width: 350px;
        margin: 10px auto;
    }
}