.contact{
    margin-bottom: 60px;
    padding-top: 50px;
}

.card0{
    box-shadow: 0px 4px 8px 0px #757575;
    border-radius: 0px ;
}

.card2{
    margin: 0px 40px;
}

.card1 .image{
    padding: 30px;
    margin-top: 30px;
    height: 100%;
    width: 100%;
}

.line{
    height: 1px;
    width: 45%;
    margin-top: 10px;
    background-color: #e0e0e0;
}

.border-line{
    border-right: 1px solid #e0e0e0;
}

.or{
    display: flex;
    width: 10%;
    font-weight: bold;
}

::placeholder{
    color: #e0e0e0;
    opacity: 1;
    font-weight: 300;
}

input,textarea{
    padding: 10px 12px;
    border: 1px solid lightgray;
    border-radius: 2px;
    margin-bottom: 5px;
    margin-top: 2px;
    width: 100%;
    color: #138781;
    font-size: 1rem;
    letter-spacing: 1px;
}

input:focus, textarea:focus {
    border: none !important;
    outline: 1px solid #138781 !important;
    outline-width: 0px;
}

.button:focus{
    box-shadow: none;
    outline-width: 0px;
}

.button{
    background-color: #1e1e2c;
    color: white;
    border-radius: 2px;
    margin-top: 15px;
    padding: 10px;
}

.button:hover{
    background-color: #000;
    color: white;
}

.card0 .icon{
    cursor: pointer;
}

/* Dark Mode */

#dark .contact .card0{
    box-shadow: none; 
    background-color: rgba(0, 0, 0, 0.15); 
    
}

#dark .contact .card2 {
    background-color: rgba(0, 0, 0, 0.15);  
    color: white;
}

/* mobile view */

@media (max-width:600px){
    .contact .card2{
        margin: 0px;
    }

    .contact .card2 .or{
        padding: 0px 5px 0px 5px;
    }

    .contact .card2 h6{
        display: flex;
        align-items: center;
        justify-content: center;
    }
}