.home-container{
    height: 300px;
    color:white;
}
.home-content h2{
    padding:5px;
    padding-top: 70px;
}

.home-content h1{
    color: #e0b50f;
    padding:5px;
}

.home-buttons {
    margin-top: 20px;
}

.home-buttons .btn-hire{
    background-color: #3b8ff3;
    color: white;
    width: 160px;
}

.home-buttons .btn-hire:hover{
    background-color: white;
    color: #3b8ff3;
    width: 200px;
    transition: 0.5s;
}

.home-buttons .btn-cv{
    background-color: #f29f37;
    margin-left: 15px;
    color: white;
}

.home-buttons .btn-cv:hover{
    opacity: 80%;
    transition: 0.5s;
}

.theme-btn{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 50px;
    width: 50px;
    background-color: #1e1e1e;
    margin-top: 20px;
    text-align: center;
    position: fixed;
    cursor: pointer;
    z-index: 1;
    right: 0;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
}

/* mobile view */

@media (max-width:600px){
    .home-container{
        margin-top: 50px;
    }
    .home-container h2, h1{
        font-size: 24px;
    }
}