.work {
    background-color: #1387812a;
    padding: 60px 0px;
}

.work-exp h2 {
    font-weight: bold;
    color: #1e1e2c;
}

.work-exp hr {
    color: #138781;
    width: 400px;
    margin: 10px auto;
}

.work-exp .vertical-timeline-element-title {
    color: #138781;
}

.work-exp .vertical-timeline-element-subtitle{
    color: rgb(182, 174, 174);
}
/* Mobile View */

@media (max-width:600px){
    .work-exp hr{
        color: #138781;
        width: 350px;
        margin: 10px auto;
    }
}